import React from "react";

const Comments = ({ comments }) => {
  return (
    <>
      <h3>comments</h3>
      {comments.map((comment, key) => (
        <div className="comment">
          <h3>{comment.username}</h3>
          <div>{comment.text}</div>
        </div>
      ))}
    </>
  );
};

export default Comments;
