import React, { useState, useEffect } from "react";
import articles from "./article-content";
import NotFound from "../pages/notFound";
import ArticleList from "../components/article-list";
import Comments from "../components/comments";
import Upvotes from "../components/upvotes";
import AddCommentSection from "../components/add-comment";

const Article = ({ match }) => {
  const name = match.params.name;
  const article = articles.find((article) => article.name === name);
  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] }); //react hooks

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      setArticleInfo(body);
    };
    fetchData();
  }, [name]);

  if (!article) return <NotFound />;

  const otherArticles = articles.filter((article) => article.name !== name);

  return (
    <>
      <div className="page-body">
        <h1>{article.title}</h1>
        <Upvotes
          articleName={name}
          setArticleInfo={setArticleInfo}
          upvotes={articleInfo.upvotes}
        />
        {article.content.map((p, key) => (
          <p key={key}>{p}</p>
        ))}

        <Comments comments={articleInfo.comments} />
        <br />
        <AddCommentSection articleName={name} setArticleInfo={setArticleInfo} />
        <br />
        <br />
        <div>
          <h3>Other Articles</h3>
          <ArticleList articles={otherArticles} />
        </div>
      </div>
    </>
  );
};
export default Article;
